import { forwardRef } from 'react';
import { DataGrid, DataGridProps, GridOverlay } from '@mui/x-data-grid';

function MUINoRowsOverlay() {
  return <GridOverlay></GridOverlay>;
}

export default forwardRef<HTMLDivElement, DataGridProps>(function MUIDataGrid(
  props,
  ref,
) {
  return (
    <DataGrid
      disableRowSelectionOnClick
      autoHeight
      hideFooterPagination
      hideFooter
      slots={{
        noRowsOverlay: MUINoRowsOverlay,
      }}
      {...props}
      ref={ref}
    />
  );
});
