import Checkbox, { CheckboxProps } from '@mui/material/Checkbox';
import FormControlLabel, {
  FormControlLabelProps,
} from '@mui/material/FormControlLabel';

interface MUILabelCheckboxProps extends CheckboxProps {
  label?: string;
  formControlProps?: FormControlLabelProps;
}

export default function MUILabelCheckbox({
  label,
  formControlProps,
  ...props
}: MUILabelCheckboxProps) {
  return (
    <FormControlLabel
      control={<Checkbox {...props} />}
      label={label}
      {...formControlProps}
    />
  );
}
