import ContentPasteIcon from '@mui/icons-material/ContentPaste';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import TreatmentScheduleModal from './TreatmentScheduleModal';
import TreatmentSessionModal from './TreatmentSessionModal';
import modalControl from 'app/modal';

interface HeadFunctionsProps {
  onSelect?: (type: 'RECORD' | 'BILL') => void;
}
export default function HeadFunctions({ onSelect }: HeadFunctionsProps) {
  return (
    <Box display="flex" justifyContent="center" gap={1.5} flexWrap="wrap">
      <Button
        variant="outlined"
        startIcon={<ContentPasteIcon />}
        onClick={() => onSelect?.('RECORD')}
      >
        Bệnh án
      </Button>
      <Button
        variant="outlined"
        startIcon={<ContentPasteIcon />}
        onClick={() => onSelect?.('BILL')}
      >
        Phiếu điều trị (Y lệnh)
      </Button>
      <Button variant="outlined" startIcon={<ContentPasteIcon />}>
        Toa thuốc ra viên
      </Button>
      <Button
        variant="outlined"
        startIcon={<ContentPasteIcon />}
        onClick={() =>
          modalControl.open(TreatmentSessionModal, undefined, {
            maxWidth: 'lg',
            title: 'Danh sách đợt điều trị',
          })
        }
      >
        Đợt điều trị
      </Button>
      <Button
        variant="outlined"
        startIcon={<ContentPasteIcon />}
        onClick={() => {
          modalControl.open(TreatmentScheduleModal, undefined, {
            maxWidth: 'lg',
            title: 'In lịch CT mô phỏng',
          });
        }}
      >
        In lịch điều trị
      </Button>
    </Box>
  );
}
