import { useState } from 'react';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import SaveIcon from '@mui/icons-material/Save';
import SummarizeIcon from '@mui/icons-material/Summarize';
import { Box, Button, Grid, Tab, Tabs, TextField } from '@mui/material';
import MedicalRecordSummaryModal from './MedicalRecordSummaryModal';
import MedicalRecordTab1 from './MedicalRecordTab1';
import MedicalRecordTab2 from './MedicalRecordTab2';
import modalControl from 'app/modal';
import MUISelect from 'common/components/controls/MUISelect';

interface TabType {
  type: 1 | 2 | 3 | 4;
  label: string;
  content: JSX.Element;
}
const tabs: TabType[] = [
  {
    type: 1,
    label: '1. Hành chính quản lý người bệnh',
    content: <MedicalRecordTab1 />,
  },
  {
    type: 2,
    label: '2. Chuẩn đoán',
    content: <MedicalRecordTab2 />,
  },
  {
    type: 3,
    label: '3. Tình trạng ra viện',
    content: <div>3</div>,
  },
  {
    type: 4,
    label: '4. Chứng nhận phẩu thuật',
    content: <div>4</div>,
  },
];

export default function MedicalRecordTab() {
  const [tab, setTab] = useState<TabType>(tabs[0]);

  return (
    <Grid container spacing={1.5}>
      <Grid
        item
        xs={12}
        md={3.5}
        display="flex"
        flexDirection="column"
        gap={1.5}
      >
        <TextField label="Số bệnh án" />
        <TextField label="Mã y tế" />
        <MUISelect options={[]} label="PL chuyên khoa" />
        <MUISelect options={[]} label="PL bệnh" />
        <MUISelect options={[]} label="Đề tài nghiên cứu" />
      </Grid>
      <Grid
        item
        xs={12}
        md={3.5}
        display="flex"
        flexDirection="column"
        gap={1.5}
      >
        <TextField label="Số lưu trữ" />
        <MUISelect options={[]} label="Loại bệnh án" />
        <Box display="flex" gap={1.5}>
          <MUISelect options={[]} label="PL chuyên khoa" />
          <TextField label="Số phim" />
        </Box>
        <MUISelect options={[]} label="Nguyên nhân" />
        <MUISelect options={[]} label="Khoa điều trị" />
      </Grid>
      <Grid item xs={12} md={5} display="flex" flexDirection="column" gap={1.5}>
        <MUISelect options={[]} label="Người lập" />
        <MUISelect options={[]} label="Trạng thái bệnh án" />
        <TextField multiline rows={5.5} />
      </Grid>

      <Grid item xs={12}>
        <Box mt={2}>
          <Tabs value={tab} onChange={(_, t) => setTab(t)}>
            {tabs.map(t => (
              <Tab label={t.label} value={t} />
            ))}
          </Tabs>
          <Box mt={2}>{tab.content}</Box>
        </Box>
      </Grid>
    </Grid>
  );
}

export function MedicalRecordTabToolbar() {
  return (
    <Box
      display="flex"
      gap={1.5}
      mt={{
        xs: 1.5,
        md: 0,
      }}
    >
      <Button size="small" variant="contained" startIcon={<EditIcon />}>
        Sửa
      </Button>
      <Button size="small" variant="contained" startIcon={<DeleteIcon />}>
        Xóa
      </Button>
      <Button size="small" variant="contained" startIcon={<SaveIcon />}>
        Lưu
      </Button>
      <Button
        size="small"
        variant="contained"
        startIcon={<SummarizeIcon />}
        onClick={() =>
          modalControl.open(MedicalRecordSummaryModal, undefined, {
            maxWidth: 'lg',
            title: 'Chi tiết hồ sơ bệnh án',
          })
        }
      >
        Tổng kết bệnh án
      </Button>
    </Box>
  );
}
