import { MedicalRecordModel } from 'services/medical-record';

function makeid(length) {
  let result = '';
  const characters =
    'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
  const charactersLength = characters.length;
  let counter = 0;
  while (counter < length) {
    result += characters.charAt(Math.floor(Math.random() * charactersLength));
    counter += 1;
  }
  return result;
}

export const medicalRecords: MedicalRecordModel[] = Array(100)
  .fill(0)
  .map((_, i) => ({
    id: `id-${i}`,
    patient_code: (10000000 + i).toString(),
    reception_code: (20000000 + i).toString(),
    fullname: 'Nguyện Thị ' + makeid(5),
    gender: 'Nữ',
    birthday: '1985',
    phone: '0901111159',
    bhyt: {
      code: 'FA1012311312322',
      start_date: '01-01-2000',
      end_date: '01-01-2010',
    },
    queue_number: i + 1,
    room: 'Phỏng hóa trị',
    register_from: 'Kiosk',
    has_profile: true,
    has_payment: true,
  }));
