import { useState } from 'react';
import Box from '@mui/material/Box';
import Tab from '@mui/material/Tab';
import Tabs from '@mui/material/Tabs';
import HeadFunctions from './components/HeadFunctions';
import MedicalRecordList from './components/MedicalRecordList';
import MedicalRecordTab, {
  MedicalRecordTabToolbar,
} from './components/MedicalRecordTab';
import TreatmentBill from './components/TreatmentBill';
import PageContent from 'modules/@core/components/Layout/PageContent';

interface TabType {
  type: 'LIST' | 'RECORD' | 'BILL';
  label: string;
  content: JSX.Element;
  toolbar?: JSX.Element;
}
const allTabs: TabType[] = [
  {
    type: 'LIST',
    label: 'Danh sách bệnh án tại Khoa',
    content: <MedicalRecordList />,
  },
  {
    type: 'RECORD',
    label: 'Bệnh án',
    content: <MedicalRecordTab />,
    toolbar: <MedicalRecordTabToolbar />,
  },
  {
    type: 'BILL',
    label: 'Phiếu điều trị(Y lệnh)',
    content: <TreatmentBill />,
  },
];

export default function MedicalRecordManagerPage() {
  const [tabs, setTabs] = useState(allTabs.slice(0, 1));
  const [tab, setTab] = useState<TabType>(tabs[0]);

  // Render
  return (
    <PageContent display="flex" flexDirection="column" height="100%">
      <HeadFunctions
        onSelect={type => {
          const currentTab = allTabs.find(t => t.type === type);
          if (!currentTab) return;
          setTabs([allTabs[0], currentTab]);
          setTab(currentTab);
        }}
      />

      <Box mt={2}>
        <Box
          display="flex"
          flexDirection={{
            xs: 'column',
            md: 'row',
          }}
          justifyContent="space-between"
          alignItems="center"
          borderBottom={1}
          borderColor="primary.main"
        >
          <Tabs value={tab} onChange={(_, t) => setTab(t)}>
            {tabs.map(t => (
              <Tab label={t.label} value={t} />
            ))}
          </Tabs>
          {tab.toolbar}
        </Box>

        <Box mt={2}>{tab.content}</Box>
      </Box>
    </PageContent>
  );
}
